.task-list {
	min-width: 0em;
	width: 0em;
	border-right: 1px solid var(--white-color);
	background-color: rgba(48, 51, 54);
	position: relative;
	right: 13em;
	transition: 0.5s;
	overflow: hidden;
	padding: 3em 0.5em;
}

.task-list_active {
	right: 0em;
	min-width: 13em;
}

.task-list__element {
	background-color: transparent;
	color: inherit;
}

@media (max-width: 414px) {
	.task-list {
		position: absolute;
		top: 5em;
		left: -100%;
		height: 100%;
		width: 0px;
		z-index: 665;
	}
	.task-list_active {
		left: 0px;
		min-width: 100%;
	}
}
