.task-board {
	width: 100%;
	padding: 2em 2em 14em 3em;
	display: flex;
	flex-wrap: wrap;
	justify-content: flex-start;
	align-content: flex-start;
	align-items: center;
}

.task-board__task-card {
	margin-right: 3em;
	margin-bottom: 3em;
}

@media (max-width: 768px) {
	.task-board {
		justify-content: space-around;
	}
	.task-board__task-card {
		margin-right: 3em;
	}
}
@media (max-width: 414px) {
	.task-board {
		flex-direction: column;
		flex-wrap: nowrap;
	}
	.task-board__task-card {
		margin-right: 0em;
	}
}
