.container {
	display: flex;
	width: 100%;
	background-color: var(--main-bg-color);
}

.burger {
	padding: 0.4em;
	margin-left: 0.5em;
	margin-top: 0.5em;
	height: 1.9em;
	align-self: flex-start;
	background-color: white;
	border-radius: 0.3em;
	position: absolute;
	z-index: 667;
}

.burger-line {
	transition: 0.5s;
}

.burger-line:nth-child(1) {
	width: 24px;
	position: relative;
	bottom: 3px;
	height: 3px;
	display: block;
	background-color: black;
}

.burger-line:nth-child(2) {
	position: relative;
	width: 24px;
	height: 3px;
	display: block;
	background-color: black;
}

.burger-line:nth-child(3) {
	position: relative;
	top: 3px;
	width: 24px;
	height: 3px;
	display: block;
	background-color: black;
}

.burger-active .burger-line:nth-child(1) {
	top: 3px;
	transform: rotateZ(-45deg);
}

.burger-active .burger-line:nth-child(3) {
	top: -3px;
	transform: rotateZ(45deg);
}

.burger-active .burger-line:nth-child(2) {
	transform: rotateY(90deg);
}
