textarea[class~="edittable-label__input"] {
	resize: none;
	width: 100%;
	height: 100%;
	background-color: transparent;
	color: white;
	text-decoration: underline;
	text-decoration-thickness: 1px;
}

textarea[class~="edittable-label__input"]::placeholder {
	text-decoration: underline;
	text-decoration-thickness: 1px;
}

.edittable-label {
	height: 100%;
	width: 100%;
}
