@import url(https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,100;0,300;0,400;0,500;0,700;1,400&display=swap);
* {
	padding: 0;
	margin: 0;
	border: 0;
}
*,
*:before,
*:after {
	box-sizing: border-box;
}
:focus,
:active {
	outline: none;
}
a:focus,
a:active {
	outline: none;
}

nav,
footer,
header,
aside {
	display: block;
}

html,
body {
	height: 100%;
	width: 100%;
	font-size: 100%;
	line-height: 1;
	font-size: 14px;
	-ms-text-size-adjust: 100%;
	-moz-text-size-adjust: 100%;
	-webkit-text-size-adjust: 100%;
}
input,
button,
textarea {
	font-family: inherit;
}

input::-ms-clear {
	display: none;
}
button {
	cursor: pointer;
}
button::-moz-focus-inner {
	padding: 0;
	border: 0;
}
a,
a:visited {
	text-decoration: none;
}
a:hover {
	text-decoration: none;
}
ul li {
	list-style: none;
}
img {
	vertical-align: top;
}

h1,
h2,
h3,
h4,
h5,
h6 {
	font-size: inherit;
	font-weight: 400;
}

*,
*::after,
*::before {
	font-size: 20px;
	font-family: "Roboto", sans-serif;
}

#root {
	--main-bg-color: #3c4043;
	--header-color: rgb(32, 33, 36);
	--white-color: #dadce0;
	--card-header-color: #b85c38;
	--card-container-color: #2d2424;
	color: var(--white-color);
}

.App {
	background-color: var(--header-color);
	min-height: 100vh;
	display: flex;
	flex-direction: column;
	justify-content: space-between;
}

.App__container {
	flex: 1 1;
}

.card-container {
	padding: 0.5em;
	display: flex;
	flex-direction: column;
	justify-content: space-between;
}

textarea[name="card-container__input"] {
	resize: none;
	width: 100%;
	height: 100%;
	background-color: transparent;
	color: white;
	text-decoration: underline;
	text-decoration-thickness: 1px;
}

textarea[name="card-container__input"]::-webkit-input-placeholder {
	text-decoration: underline;
	text-decoration-thickness: 1px;
}

textarea[name="card-container__input"]:-ms-input-placeholder {
	text-decoration: underline;
	text-decoration-thickness: 1px;
}

textarea[name="card-container__input"]::placeholder {
	text-decoration: underline;
	text-decoration-thickness: 1px;
}

.card-container__description {
	height: 100%;
}

textarea[class~="edittable-label__input"] {
	resize: none;
	width: 100%;
	height: 100%;
	background-color: transparent;
	color: white;
	text-decoration: underline;
	text-decoration-thickness: 1px;
}

textarea[class~="edittable-label__input"]::-webkit-input-placeholder {
	text-decoration: underline;
	text-decoration-thickness: 1px;
}

textarea[class~="edittable-label__input"]:-ms-input-placeholder {
	text-decoration: underline;
	text-decoration-thickness: 1px;
}

textarea[class~="edittable-label__input"]::placeholder {
	text-decoration: underline;
	text-decoration-thickness: 1px;
}

.edittable-label {
	height: 100%;
	width: 100%;
}

.card-header {
	padding: 0.5em;
	height: 4em;
	background-color: var(--card-header-color);
	position: relative;
	display: flex;
	justify-content: space-between;
}

.icon-close {
	position: absolute;
	padding: 0.1em;
	right: 0px;
	top: 0px;
	align-self: flex-start;
	transition: background-color 0.2s;
	border-radius: 0.3em;
}

.icon-close:hover {
	background-color: rgba(0, 0, 0, 0.1);
}

textarea[class~="title-input"]::-webkit-input-placeholder {
	color: var(--white-color);
	text-decoration: underline;
	text-decoration-thickness: 1px;
}

textarea[class~="title-input"]:-ms-input-placeholder {
	color: var(--white-color);
	text-decoration: underline;
	text-decoration-thickness: 1px;
}

textarea[class~="title-input"]::placeholder {
	color: var(--white-color);
	text-decoration: underline;
	text-decoration-thickness: 1px;
}

.task-card {
	width: 12em;
	height: 14em;
	border: 2px solid var(--card-header-color);
	border-radius: 0.2em;
	display: flex;
	flex-direction: column;
	align-content: space-between;
	background-color: var(--card-container-color);
	transition: 0.1s;
}

.task-card:hover {
	-webkit-transform: translateY(-0.1em);
	        transform: translateY(-0.1em);
	box-shadow: 0 0 8px 0 rgb(255 255 255 / 8%), 0 0 15px 0 rgb(255 255 255 / 2%), 0 0 20px 4px rgb(255 255 255 / 6%);
}

.task-card__card-container {
	flex: 1 1;
}

.task-card__date {
	font-size: 0.7em;
	padding: 0.2em 0.3em;
	margin-left: auto;
	border-left: 1px solid var(--card-header-color);
	border-top: 1px solid var(--card-header-color);
	border-top-left-radius: 0.2em;
}

.task-board {
	width: 100%;
	padding: 2em 2em 14em 3em;
	display: flex;
	flex-wrap: wrap;
	justify-content: flex-start;
	align-content: flex-start;
	align-items: center;
}

.task-board__task-card {
	margin-right: 3em;
	margin-bottom: 3em;
}

@media (max-width: 768px) {
	.task-board {
		justify-content: space-around;
	}
	.task-board__task-card {
		margin-right: 3em;
	}
}
@media (max-width: 414px) {
	.task-board {
		flex-direction: column;
		flex-wrap: nowrap;
	}
	.task-board__task-card {
		margin-right: 0em;
	}
}

.task-list {
	min-width: 0em;
	width: 0em;
	border-right: 1px solid var(--white-color);
	background-color: rgba(48, 51, 54);
	position: relative;
	right: 13em;
	transition: 0.5s;
	overflow: hidden;
	padding: 3em 0.5em;
}

.task-list_active {
	right: 0em;
	min-width: 13em;
}

.task-list__element {
	background-color: transparent;
	color: inherit;
}

@media (max-width: 414px) {
	.task-list {
		position: absolute;
		top: 5em;
		left: -100%;
		height: 100%;
		width: 0px;
		z-index: 665;
	}
	.task-list_active {
		left: 0px;
		min-width: 100%;
	}
}

.container {
	display: flex;
	width: 100%;
	background-color: var(--main-bg-color);
}

.burger {
	padding: 0.4em;
	margin-left: 0.5em;
	margin-top: 0.5em;
	height: 1.9em;
	align-self: flex-start;
	background-color: white;
	border-radius: 0.3em;
	position: absolute;
	z-index: 667;
}

.burger-line {
	transition: 0.5s;
}

.burger-line:nth-child(1) {
	width: 24px;
	position: relative;
	bottom: 3px;
	height: 3px;
	display: block;
	background-color: black;
}

.burger-line:nth-child(2) {
	position: relative;
	width: 24px;
	height: 3px;
	display: block;
	background-color: black;
}

.burger-line:nth-child(3) {
	position: relative;
	top: 3px;
	width: 24px;
	height: 3px;
	display: block;
	background-color: black;
}

.burger-active .burger-line:nth-child(1) {
	top: 3px;
	-webkit-transform: rotateZ(-45deg);
	        transform: rotateZ(-45deg);
}

.burger-active .burger-line:nth-child(3) {
	top: -3px;
	-webkit-transform: rotateZ(45deg);
	        transform: rotateZ(45deg);
}

.burger-active .burger-line:nth-child(2) {
	-webkit-transform: rotateY(90deg);
	        transform: rotateY(90deg);
}

.button {
	padding: 0.5em;
	border: 1px solid black;
	background-color: green;
	border-radius: 0.5em;
	color: white;
	transition: border-color 0.2s;
}

.button:hover {
	border-color: white;
}

.header {
	height: 5em;
	display: flex;
	align-items: center;
	border-bottom: 1px solid var(--white-color);
	padding-left: 3em;
}

.header__logo {
	font-size: 32px;
	font-weight: 500;
	margin-right: 3em;
}

.header__button {
	color: inherit;
}

.header__logo :first-child {
	margin-top: 3px;
	margin-right: 5px;
	width: 23px;
	height: 24px;
	border-radius: 3px;
	background-color: white;
}

