.card-container {
	padding: 0.5em;
	display: flex;
	flex-direction: column;
	justify-content: space-between;
}

textarea[name="card-container__input"] {
	resize: none;
	width: 100%;
	height: 100%;
	background-color: transparent;
	color: white;
	text-decoration: underline;
	text-decoration-thickness: 1px;
}

textarea[name="card-container__input"]::placeholder {
	text-decoration: underline;
	text-decoration-thickness: 1px;
}

.card-container__description {
	height: 100%;
}
