.header {
	height: 5em;
	display: flex;
	align-items: center;
	border-bottom: 1px solid var(--white-color);
	padding-left: 3em;
}

.header__logo {
	font-size: 32px;
	font-weight: 500;
	margin-right: 3em;
}

.header__button {
	color: inherit;
}

.header__logo :first-child {
	margin-top: 3px;
	margin-right: 5px;
	width: 23px;
	height: 24px;
	border-radius: 3px;
	background-color: white;
}
